import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { Calendar } from "primereact/calendar";
import { Dropdown } from "primereact/dropdown";
import { InputNumber } from "primereact/inputnumber";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { campaignActions } from "../../store/campaign/campaignSlice";
import { currencyActions } from "../../store/currency/currencySlice";
import { userActions } from "../../store/user/userSlice";
import { IAppState } from "../../models/appState";
import { ICampaign } from "../../models/campaign";
import { v4 as uuidv4 } from "uuid";
import {
  convertDateToString,
  convertStringToDate,
  convertCurrency,
} from "../../utilities/helpers";

const CampaignEdit: React.FC = () => {
  const statusData = [
    "Planning",
    "Active",
    "Inactive",
    "Complete",
    "In Queue",
    "Sending",
  ];
  const typeData = [
    "Telesales",
    "Mail",
    "Email",
    "Print",
    "Web",
    "Radio",
    "Television",
    "Newsletter",
  ];

  const defaultItem: ICampaign = {
    id: "",
    name: "",
    assignedUserId: "",
    assignedUserLastName: "",
    startDate: "",
    endDate: "",
    status: "",
    impressions: null,
    currencyId: "",
    budget: null,
    actualCost: null,
    expectedCost: null,
    expectedRevenue: null,
    campaignType: "",
    objective: "",
    description: "",
  };
  const navigateTo = "/campaigns";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const params = useParams();
  const memo = useRef(true);

  const [
    campaign,
    isFetching,
    isError,
    campaigns,
    currencies,
    users,
    isProfile,
    profile,
  ] = useAppSelector(
    (state: IAppState) => [
      state.campaigns.campaign,
      state.campaigns.isFetching,
      state.campaigns.isError,
      state.campaigns.campaigns,
      state.currencies.currencies,
      state.users.users,
      state.users.isProfile,
      state.users.profile,
    ],
    shallowEqual
  );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      dispatch(currencyActions.getCurrencies());
      dispatch(userActions.getUsers());
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(campaignActions.getCampaignById(id));
        }
      }
    }
  }, [params, dispatch]);

  useEffect(() => {
    if (
      params.mode !== "edit" &&
      isProfile &&
      profile &&
      profile.currencyId &&
      item.currencyId === "" &&
      profile.currencyId !== item.currencyId
    ) {
      setItem({ ...item, currencyId: profile.currencyId });
    }
  }, [params, isProfile, profile, item]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== campaign?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(campaign);
    }
  }, [params, item, campaign, isFetching, isError]);

  const isNameValid = useCallback(
    (name: string) => {
      let result = true;
      if (item.id.length === 0) {
        if (
          campaigns.find((x) => x.name?.toLowerCase() === name?.toLowerCase())
        ) {
          result = false;
        }
      } else {
        if (
          campaigns.find(
            (x) =>
              x.name?.toLowerCase() === name?.toLowerCase() && x.id !== item.id
          )
        ) {
          result = false;
        }
      }

      return result;
    },
    [item.id, campaigns]
  );

  useEffect(() => {
    if (!dirty) {
      return;
    }

    let isValid = true;
    const filter = [
      "id",
      "assignedUserId",
      "assignedUserLastName",
      "startDate",
      "objective",
      "description",
    ];
    for (const [key, value] of Object.entries(item)) {
      if (filter.indexOf(key) > -1) {
        continue;
      }

      if (typeof value === "string") {
        if (value?.trim().length === 0) {
          isValid = false;
          break;
        }
      }
    }

    setValid(isValid && isNameValid(item.name || ""));
  }, [dirty, item, isNameValid]);

  const convertAmounts = (value: any) => {
    const id = item.currencyId ? item.currencyId : "";
    const budget = convertCurrency(id, value, item.budget, currencies);
    const actualCost = convertCurrency(id, value, item.actualCost, currencies);

    const expectedRevenue = convertCurrency(
      id,
      value,
      item.expectedRevenue,
      currencies
    );

    const expectedCost = convertCurrency(
      id,
      value,
      item.expectedCost,
      currencies
    );

    return {
      currencyId: value,
      budget,
      actualCost,
      expectedRevenue,
      expectedCost,
    };
  };

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    if (tname === "assignedUserId") {
      const user = users.find((value) => value.id === tvalue);
      if (user) {
        setItem({
          ...item,
          [tname]: tvalue,
          assignedUserLastName: user.lastName,
        });
      }
    } else if (tname === "startDate" || tname === "endDate") {
      setItem({ ...item, [tname]: convertDateToString(tvalue) });
    } else if (tname === "currencyId") {
      setItem({ ...item, ...convertAmounts(tvalue) });
    } else {
      setItem({ ...item, [tname]: tvalue });
    }

    if (tname === "name") {
      const isValidName = isNameValid(tvalue);
      setErrors({
        ...errors,
        nameInvalid: !isValidName,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    } else if (tname === "endDate") {
      if (tvalue) {
        if (tvalue.toString().trim().length === 0) {
          setErrors({ ...errors, endDate: true });
        } else {
          if (item.startDate) {
            const startDate = new Date(item.startDate);
            const endDate = new Date(tvalue);
            setErrors({
              ...errors,
              endDate: false,
              startDateMoreThanEndDate: startDate > endDate,
            });
          } else {
            setErrors({ ...errors, endDate: false });
          }
        }
      } else {
        setErrors({ ...errors, endDate: true });
      }
    }
  };

  const onSaveClick = async () => {
    if (subtitle !== "Edit") {
      item.id = uuidv4();
    }

    dispatch(campaignActions.updateCampaign(item));
    setTimeout(() => {
      dispatch(campaignActions.getCampaigns());
    }, 300);
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">Campaign: {subtitle}</h3>
      <div className="grid">
        <div className="col-12">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Campaign</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="CampaignName">
                  Campaign Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="CampaignName"
                  name="name"
                  maxLength={50}
                  value={item.name}
                  onChange={onChange}
                />
                {(errors as any)?.name && (
                  <Message severity="error" text="Campaign Name is required." />
                )}
                {(errors as any)?.nameInvalid && (
                  <Message
                    severity="error"
                    text="Campaign Name already exists."
                  />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="AssignedTo">Assigned To</label>
              </div>
              <div className="col">
                <Dropdown
                  id="AssignedTo"
                  name="assignedUserId"
                  options={users}
                  optionLabel="lastName"
                  optionValue="id"
                  value={item.assignedUserId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Status">
                  Status <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <Dropdown
                  id="Status"
                  name="status"
                  options={statusData}
                  value={item.status}
                  onChange={onChange}
                  className="p-combobox"
                />
                {(errors as any)?.status && (
                  <Message severity="error" text="Status is required." />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Type">
                  Type <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <Dropdown
                  id="Type"
                  name="campaignType"
                  options={typeData}
                  value={item.campaignType}
                  onChange={onChange}
                  className="p-combobox"
                />
                {(errors as any)?.campaignType && (
                  <Message severity="error" text="Type is required." />
                )}
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="StartDate">Start Date</label>
              </div>
              <div className="col">
                <Calendar
                  id="StartDate"
                  name="startDate"
                  value={convertStringToDate(item.startDate)}
                  onChange={onChange}
                  showButtonBar
                  showIcon
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="EndDate">
                  End Date <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <Calendar
                  id="EndDate"
                  name="endDate"
                  value={convertStringToDate(item.endDate)}
                  onChange={onChange}
                  showButtonBar
                  showIcon
                  className="p-combobox"
                />
                {(errors as any)?.endDate && (
                  <Message severity="error" text="End Date is required." />
                )}
                {(errors as any)?.startDateMoreThanEndDate && (
                  <Message
                    severity="error"
                    text="Start Date cannot be more than End Date."
                  />
                )}
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Finance</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Currency">
                  Currency <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <Dropdown
                  id="Currency"
                  name="currencyId"
                  options={currencies}
                  optionLabel="name"
                  optionValue="id"
                  value={item.currencyId}
                  onChange={onChange}
                  className="p-combobox"
                />
                {(errors as any)?.currencyId && (
                  <Message severity="error" text="Currency is required." />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Impressions">Impressions</label>
              </div>
              <div className="col">
                <InputNumber
                  id="Impressions"
                  name="impressions"
                  value={item.impressions}
                  onValueChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Budget">Budget</label>
              </div>
              <div className="col">
                <InputNumber
                  id="Budget"
                  name="budget"
                  value={item.budget}
                  onValueChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="ActualCost">Actual Cost</label>
              </div>
              <div className="col">
                <InputNumber
                  id="ActualCost"
                  name="actualCost"
                  value={item.actualCost}
                  onValueChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="ExpectedRevenue">Expected Revenue</label>
              </div>
              <div className="col">
                <InputNumber
                  id="ExpectedRevenue"
                  name="expectedRevenue"
                  value={item.expectedRevenue}
                  onValueChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="ExpectedCost">Expected Cost</label>
              </div>
              <div className="col">
                <InputNumber
                  id="ExpectedCost"
                  name="expectedCost"
                  value={item.expectedCost}
                  onValueChange={onChange}
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Objective</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <InputTextarea
              name="objective"
              value={item.objective}
              onChange={onChange}
              autoResize
            />
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Description</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <InputTextarea
              name="description"
              value={item.description}
              onChange={onChange}
              autoResize
            />
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default CampaignEdit;
